/**
 * js
 * test/test.js
 * @export
 * @class MyUtils
 */
export default class MyUtils {
  /**
   * url
   * hashhash
   * urlhttp://localhost:3000/index.html?code=0210YO6j22fLhJ0nGI5j2Fr47j20YO66&state=1212
   * hashurl:http://localhost:3000/index.html?code=0210YO6j22fLhJ0nGI5j2Fr47j20YO66#home?state=1212
   * @static
   * @returns object{}
   * @memberof MyUtils
   */
  static queryParams() {
    const hash = decodeURIComponent(window.location.hash);
    const search = decodeURIComponent(window.location.search);
    const query = search.substring(1) + '&' + hash.substring(hash.indexOf('?') + 1);
    const params = {};
    query.split('&').map(elm => elm.split('=')).forEach((value, index) => params[value[0]] = value[1]);
    return params
  }

  /**
   * safari
   * fixDate('2013-09-12T12:33:30+0000'),fixDate('2013-09-12T12:33:30+0800')
   * @static
   * @param {string} dataStr 
   * @returns string
   * @memberof MyUtils
   */
  static fixDate(dataStr) {
    if (!dataStr) {
      return
    }
    let tempDate = new Date(dataStr);
    if (tempDate == 'Invalid Date') {
      dataStr = dataStr.replace(/T/g, ' ').replace(/-/g, '/');
      tempDate = new Date(dataStr)
    }
    tempDate.toLocaleDateString();
    return tempDate
  }


  /**
   * 
   * format(new Date(),'yyyy-MM-dd hh:mm:ss');
   * safarifixDate
   * @static
   * @param {Date object or date string} date 
   * @param {string} fmt 
   * @returns string
   * @memberof MyUtils
   */
  static format(date, fmt) {
    if (typeof fmt !== 'string') {
      return
    }
    const data = new Date(date);
    const obj = {
      'y+': data.getFullYear(),
      'M+': data.getMonth() + 1,
      'd+': data.getDate(),
      'h+': data.getHours(),
      'm+': data.getMinutes(),
      's+': data.getSeconds(),
    };
    for (let key in obj) {
      if (new RegExp(`(${key})`).test(fmt)) {
        if (key === 'y+') {
          fmt = fmt.replace(RegExp.$1, obj[key].toString().substring(4 - RegExp.$1.length));
        } else {
          fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? (obj[key]) : ('00' + obj[key]).substring(obj[key].toString().length));
        }
      }
    }
    return fmt
  }

  /**
   * 
   * 111111111,111
   * (?=)(?!)
   * @static
   * @param {any} num 
   * @returns 
   * @memberof MyUtils
   */
  static threeNumber(num) {
    num = '' + num;
    return num.replace(/(?!^)(?=(\d{3})+$)/g, ',')
  }

  /**
   * 
   *  obj1[prop] < obj2[prop]
   * 
   * @static
   * @param {string} prop 
   * @returns boolean
   * @memberof MyUtils
   */
  static compare(prop) {
    if (typeof prop !== 'string') {
      return
    }
    return function (obj1, obj2) {
      return obj1[prop] > obj2[prop]
    }
  }
}

// 
window.MyUtils = MyUtils;


